<template>
	<view>
		<input class="uni-input" type="text" v-model="email" value="" placeholder="请输入你要绑定的邮箱"/>
		<input class="uni-input" type="text" v-model="password" value="" placeholder="请输入密码"/>
		
		<view class="py-2 px-2">
			<button class="bg-main text-white" style="border-radius: 50rpx; border: 0;" 
			type="primary" :disabled="disabled" @click="submit">绑定</button>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				email: "",
				password: ""
			}
		},
		computed:{
			disabled(){
				return this.email == '' || this.password == '';
			}
		},
		methods: {
			// 验证
			check(){
				let rule = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
				
				if(!rule.test(this.email)){
					return uni.showToast({
						title:"邮箱格式不正确",
						icon:"none"
					})
					return false;
				}
			},
			submit(){
				if(!this.check()){
					return;
				}
				console.log('提交成功');
			}
			
		}
	}
</script>

